<template>
  <div class="vue__pages" v-if="getAllUsers">
    <Header />
    <div class="container">
      <router-view />
    </div>
  </div>
  <Loader v-else />
</template>

<script>
import Header from "@/components/Header/Header.vue";
import Loader from "@/components/Delay/Loader.vue";
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";

export default {
  components: {
    Header,
    Loader,
  },
  computed: {
    ...mapGetters(["getAllUsers"]),
  },
  methods: {
    ...mapActions(["getUsers", "getPosts", "getTodos"]),
  },

  created() {
    setTimeout(() => {
      this.getUsers();
      this.getPosts();
      this.getTodos();
    }, 1500);
  },
};
</script>
