<template>
  <div class="users">
    <h1 class="users__title">Users</h1>
    <div class="users__block">
      <div class="users__item" v-for="user in users" :key="user.id">
        <p class="users__name">{{ user.name }}</p>
        <p class="users__userName">({{ user.username }})</p>
        <p class="users__address-city">address: {{ user.address.city }}</p>
        <p class="users__phone">{{ user.phone }}</p>
        <p class="users__email">{{ user.email }}</p>
        <p class="users__website">site: {{ user.website }}</p>
        <p class="users__company-name">company '{{ user.company.name }}''</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  computed: {
    ...mapState(["users"]),
  },
};
</script>
